* {
  box-sizing: border-box;
}
.list {
  width: 10em;
  height: 20em;
  background-color: rgb(235, 211, 178);
  font-size: 23px;
  font-family: cursive;
  border: 4px solid white;
  margin-top: 1em;
  overflow: scroll;
}
.done {
  text-decoration: line-through;
}
.button {
  border: none;
  background-color: rgb(235, 211, 178);
  font-size: 120%;
  color: red;
  cursor: pointer;
}
ul li {
  list-style: none;
  width: fit-content;
}
body {
  background-color: antiquewhite;
}
input {
  border-radius: 10px;
  font-weight: bolder;
  font-size: 17px;
}
.addBtn {
  font-size: 17px;
  border-radius: 10px;
  background-color: antiquewhite;
  color: rgb(92, 85, 85);
}
h1 {
  font-size: 2;
  font-family: fantasy;
  width: fit-content;
  height: fit-content;
  border: 2px solid white;
  padding: 15px;
  margin-left: 5px;
  background-color: rgb(241, 231, 219);
}
.clearBtn {
  font-size: 150%;
  color: red;
  border-radius: 25px;
  box-shadow: 3px 4px rgb(0, 0, 0);
  margin-left: 4%;
}
ul li:hover {
  color: rgb(190, 116, 116);
}
